<template>
  <v-row>
    <v-col class="py-0 px-4">
      <v-card elevation="0">
        <v-card-title>
          <v-row justify="space-between">
            <v-col cols="12" sm="4">
              История Crash
            </v-col>
            <v-col cols="12" sm="4">
              <v-select
                label="Категория" 
                :items="['Все', 'С подкруткой', 'Без подкрутки']"
                v-model="group_type"
              ></v-select>
            </v-col>
            <v-col cols="12" sm="4">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-title>
      </v-card>
      <v-data-table
        :headers="headers"
        :items="filteredItems"
        :search="search"
      >
        <template v-slot:item.action="props">
          <v-btn 
            :to="{ name: 'crash-games', params: { id: props.item.id, data: props.item }}" 
            icon
          >
            <v-icon small>
              mdi-information-outline
            </v-icon>
          </v-btn>
        </template> 
      </v-data-table> 
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    group_type: null,
    search: '',
    headers: [
      { text: 'ID', align: 'left', value: 'id' },
      { text: 'Сумма ставок', value: 'count' },
      { text: 'Сумма выигрышей', value: 'amount' },
      { text: 'Прибыль', value: 'profit' },
      { text: 'Коэффициент', value: 'k' },
      { text: 'Подкрутка', value: 'twist' },
      { text: 'Действия', value: 'action', sortable: false }
    ],
    items: [
      { 
        id: 1,
        count: 4,
        amount: 1000,
        profit: 2000,
        k: 1.8,
        twist: 'Нет',
        category: 'Без подкрутки'
      },
      { 
        id: 2,
        count: 10,
        amount: 100,
        profit: 2000,
        k: 2,
        twist: 'Да',
        category: 'С подкруткой'
      },
      { 
        id: 3,
        count: 2,
        amount: 2000,
        profit: 2000,
        k: 4,
        twist: 'Нет',
        category: 'Без подкрутки'
      },
      { 
        id: 4,
        count: 0,
        amount: 1000,
        profit: 2000,
        k: 1.1,
        twist: 'Нет',
        category: 'Без подкрутки'
      },
      { 
        id: 5,
        count: 6,
        amount: 1000,
        profit: 2000,
        k: 1,
        twist: 'Нет',
        category: 'Без подкрутки'
      },
      { 
        id: 6,
        count: 4,
        amount: 1000,
        profit: 2000,
        k: 2.6,
        twist: 'Нет',
        category: 'Без подкрутки'
      },  
    ]
  }),
  computed: {
    filteredItems() {
      return this.items.filter((item) => {
        return (this.group_type == 'Все') ? item : !this.group_type || (item.category.toLowerCase() === this.group_type.toLowerCase());
      });
    }
  }
}
</script>
